 * @LastEditTime: 2020-12-24 12:26:45
-->
<template>
    <view class="things_user_collect">
        <!-- tabbar -->
        <cu-custom><block slot="content">个人收藏</block></cu-custom>
        <!-- 导航栏 -->
        <scroll-view scroll-x class="bg-white nav">
            <view class="flex text-center navBox">
                <view class="cu-item flex-sub" :class="item.id==tabCur?'things_active_color':''" v-for="(item,index) in tabData" :key="index" @tap="tabSelect" :data-id="item.id">
                    {{item.name}}
                </view>
            </view>
        </scroll-view>
        <!-- 货物列表 -->
        <scroll-view v-if="tabCur == 1" class="things_width_full freight_list" scroll-y="true" @scrolltolower="onReachBottomFreight">
            <view class="flex justify-between align-start things_width_full freight_item" v-for="(item,index) in freightData.list" :key="item.id">
                <view class="things_width_full box_top" :data-id="item.id" @tap="linkToFreightInfo">
                    <view class="start_address">装载地点：{{item.loadingAddress}}</view>
                    <view class="end_address">卸货地点：{{item.unloadingAddress}}</view>
                    <view class="flex justify-between align-center double_box">
                        <view class="freight_type">货物类型：{{item.goodsType}}</view>
                        <view class="freight_weight">重量：{{item.weight}}吨</view>
                    </view>
                    <view class="transport_money">运输价格：￥{{item.unitPrice}}</view>
                </view>
                <!-- 按钮组 -->
                <view class="things_width_full flex justify-between align-center btn_box">
                    <button class="cu-btn bg-white" :disabled="buttonType" :data-index="index" :data-id="item.id" @tap="delFreightFun"><image class="del_icon" src="../../../../../static/images/global/del.png" mode="widthFix"></image><text class="btn_text">删除</text></button>
                    <button class="cu-btn bg-white" :disabled="buttonType" :data-id="item.id" @tap="linkToUpdateFreight"><image class="update_icon" src="../../../../../static/images/global/update.png" mode="widthFix"></image><text class="btn_text">修改</text></button>
                </view>
            </view>
            <!-- 新增按钮 -->
            <view class="things_width_full flex justify-center align-center add_btn_box">
                <button class="things_width_full cu-btn" :disabled="buttonType" @tap="linkToAddFreight">新增货物</button>
            </view>
        </scroll-view>
        <!-- 货车列表 -->
        <scroll-view v-if="tabCur == 2" class="things_width_full car_list" scroll-y="true" @scrolltolower="onReachBottomCar">
            <view class="flex justify-between align-start things_width_full car_item" v-for="(item,index) in carData.list" v-bind:key="item.id">
                <view class="things_width_full box_top" :data-id="item.id" @tap="linkToCarInfo">
                    <view class="flex justify-between align-center double_box">
                        <view class="number_plate">车牌号：{{item.cubicNumber}}</view>
                        <view class="car_type">车辆类型：{{item.carsType}}</view>
                    </view>
                    <view class="other_number_plate">挂车牌号：{{item.listingCarNumber}}</view>
                    <view class="transport_money">运输价格(单价)：￥{{item.unitPrice}}</view>
                    <view class="car_company">所属公司：{{item.carCompany}}</view>
                </view>
                <!-- 按钮组 -->
                <view class="things_width_full flex justify-between align-center btn_box">
                    <button class="cu-btn bg-white" :data-index="index" :data-id="item.id" @tap="delCarFun"><image class="del_icon" src="../../../../../static/images/global/del.png" mode="widthFix"></image><text class="btn_text">删除</text></button>
                    <button class="cu-btn bg-white" :data-id="item.id" @tap="linkToUpdateCar"><image class="update_icon" src="../../../../../static/images/global/update.png" mode="widthFix"></image><text class="btn_text">修改</text></button>
                </view>
            </view>
            <!-- 新增按钮 -->
            <view class="things_width_full flex justify-center align-center add_btn_box">
                <button class="things_width_full cu-btn" :disabled="buttonType" @tap="linkToAddCar">新增货车</button>
            </view>
        </scroll-view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
    import { getTruckCollectionList_api,getGoodsCollectionList_api,delTruckCollection_api,delGoodsCollection_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            tabCur: 1,
            scrollLeft: 0,
            tabData: [
                {
                    id: 1,
                    name: '货物'
                },
                {
                    id: 2,
                    name: '货车'
                }
            ],
            freightData: {
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            },
            carData: {
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            },
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.base_refreshPage': function(val) {
            if (val) {
                // 更新数据
                that.clearPageFun();
                that.getFreightList();
                that.getCarList();
                that.base({name:'refreshPage',value:false});
            }
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.carData = {
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            };
            that.freightData = {
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            };
        },
        /**导航栏切换
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        tabSelect(e) {
            if (that.tabCur == e.currentTarget.dataset.id) return false;
            that.tabCur = e.currentTarget.dataset.id;
            that.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
        },
        /**获取收藏货物列表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getFreightList(e) {
            that.modal({name:'popLoading',value:true});
            let params = {
                page: that.freightData.page,
                page_size: that.freightData.page_size
            };
            getGoodsCollectionList_api(params,that).then(res=>{
                that.freightData.list = that.freightData.list.concat(res.list);
                if (res.total >= that.freightData.list.length) {
                    that.freightData.pageMore = false;
                }else{
                    that.freightData.page++;
                }
                that.modal({name:'popLoading',value:false});
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
            })
        },
        /**获取收藏货车列表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getCarList(e) {
            that.modal({name:'popLoading',value:true});
            let params = {
                page: that.carData.page,
                page_size: that.carData.page_size
            };
            getTruckCollectionList_api(params,that).then(res=>{
                that.carData.list = that.carData.list.concat(res.list);;
                if (res.total <= that.carData.list.length) {
                    that.carData.pageMore = false;
                }else{
                    that.carData.page++;
                }
                that.modal({name:'popLoading',value:false});
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
            })
        },
        /**删除收藏的货物
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        delFreightFun(e) {
            that.modal({name:'popLoading',value:true});
            let params = {
                goods_id: e.currentTarget.dataset.id
            };
            delGoodsCollection_api(params,that).then(res=>{
                that.$delete(that.freightData.list, e.currentTarget.dataset.index)
                that.modal({name:'popLoading',value:false});
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'删除成功'}});
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
            })
        },
        /**跳转到新增货物
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToAddFreight(e) {
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/user/collect/add/freight/index`,
                complete: function() {
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**跳转到货物详情
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToFreightInfo(e) {
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/user/collect/info/freight/index?goods_id=${e.currentTarget.dataset.id}`,
                complete: function() {
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**跳转到货物更新页
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToUpdateFreight(e) {
            that.modal({name:'popLoading',value:true});
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/user/collect/update/freight/index?goods_id=${e.currentTarget.dataset.id}`,
                complete: function() {
                    that.modal({name:'popLoading',value:false});
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**删除收藏的货车
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        delCarFun(e) {
            that.modal({name:'popLoading',value:true});
            let params = {
                truck_id: e.currentTarget.dataset.id
            };
            delTruckCollection_api(params,that).then(res=>{
                that.$delete(that.carData.list, e.currentTarget.dataset.index);
                that.modal({name:'popLoading',value:false});
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'删除成功'}});
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
            })
        },
        /**跳转到货车更新页
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToUpdateCar(e) {
            that.modal({name:'popLoading',value:true});
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/user/collect/update/car/index?truck_id=${e.currentTarget.dataset.id}`,
                complete: function() {
                    that.modal({name:'popLoading',value:false});
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**跳转到新增货车
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToAddCar(e) {
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/user/collect/add/car/index`,
                complete: function() {
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**跳转到货车详情
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToCarInfo(e) {
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/user/collect/info/car/index?truck_id=${e.currentTarget.dataset.id}`,
                complete: function() {
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**货物上拉加载
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        onReachBottomFreight(e) {
            if (that.freightData.pageMore) that.getFreightList();
        },
        /**货车上拉加载
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        onReachBottomCar(e) {
            if (that.carData.pageMore) that.getCarList();
        },
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        that.tabCur = options.type;
        // 请求
        that.getFreightList();
        that.getCarList();
    }
}
</script>
<style>
    page {
        width: 100vw;
        height: 100vh;
        background-color: #FFF;
    }
</style>
<style lang="scss" scoped>
    @import './index';
</style>